import React from 'react';

import mealTimes from "./mealTimes"


const Day = props=>{
    let initPos = 3*props.dayIndex


    return <div className = "box">

        <h2>{props.day}</h2>
        <div className = "day">
        {mealTimes.map((meal, index)=>{
            
            let indexNum = initPos+index 

            return <div className = "meal"
                key = {indexNum}
            >
                <h3>{meal}</h3>
                <p>{props.Protein[indexNum]}</p>
                <p>{props.Carb[indexNum]}</p>
                <p>{props.Veggie[indexNum]}</p>

            </div>
            
        })}
            
        </div>




    </div>
}
export default Day

