import React,{useState} from 'react';

const Items = props=>{

    const list = props.list

    // edit options
    const [visible, toggleVisible] = useState(true)

    const done = visible? "Edit": "Done"





    
    return <div className = "box foodGroup">
        {list.map((item, index)=>{

            return <div 
                key = {index}
                className = "itemDiv">
                 <button
                style={{display: visible? "inline": "none"}}
                className = "item"
                key = {item}
                onClick = {event=>{
                    event.preventDefault()
                    props.addToPlan(item, props.group, list)
                }}
                >{item}</button>

                <button
                    style={{display: visible? "none":"inline"}}
                    className = "item"
                    onClick = {event=>{
                        event.preventDefault()
                        props.removeFromFoods(item, props.group)

                    }}
                >DELETE {item}</button>
                
            </div>
        })}
        <div className = "editFoodGroup">

            <input 
                style={{display: visible? "none":"inline"}}
                className = "edit"
                placeholder = "Add an Item"
                onKeyDown = {event=>{
                    if (event.keyCode === 13){
                        let item = event.target.value
                        props.addToFoods(item, props.group)
                        event.target.value = ""

                    }                   
                }}
            
            />

            

            <button
                className = "done"
                onClick = {event=>{
                    event.preventDefault()
                    toggleVisible(prev=>!prev)

                }}
            >{done}</button>

        </div>

    </div>
}

export default Items
