import React from 'react';
import Items from "./Items"

const ListFoodGroups = props=>{

    return <div>
        <h1>{props.group}</h1>
        <Items 
        list = {props.list} // foodgroup array
        addToPlan = {props.addToPlan}
        group = {props.group}
        addToFoods = {props.addToFoods}
        removeFromFoods = {props.removeFromFoods}
        />
    </div>


}

export default ListFoodGroups