import React,{useState} from 'react';

const ShoppingListItem =props=>{
    const [line, toggle] = useState (false)
    let hide = false
    if(line){hide = props.hideCompleted}


    return <p 
    onClick={event=>{
        event.preventDefault()
        toggle(prev=>!prev)
    }}
    
    style = {
        {color: line?"#29C7AC":"white",
        textDecoration:line?"line-through":"none",
        display: hide?"none":"block"}
    }
    key = {props.index}>{props.unit(props.total)+" "+props.item}</p>
}

export default ShoppingListItem